import React, { useState } from 'react';
import { useAuth } from '../context/AuthContext';

/**
 * LoginForm  - 
 * 
 * 
 * 1. 
 * 2. 
 * 3. 
 * 4. 
 * 5. 
 * 
 * Props:
 * - switchToRegister: 
 */
const LoginForm = ({ switchToRegister }) => {
  // false = true = 
  const [isAdmin, setIsAdmin] = useState(false);
  // 
  const [username, setUsername] = useState('');
  // 
  const [password, setPassword] = useState('');
  // 
  const [message, setMessage] = useState('');
  // 
  const [isLoading, setIsLoading] = useState(false);
  
  // 
  const { login } = useAuth();

  /**
   * 
   * @param {Event} e - 
   */
  const handleSubmit = async (e) => {
    e.preventDefault(); // 
    setIsLoading(true);  // 
    setMessage('');      // 
    
    try {
      // 
      await login(username, password, isAdmin);
      // AuthContext 
    } catch (error) {
      // 
      setMessage(error.message || '');
    } finally {
      // 
      setIsLoading(false);
    }
  };

  return (
    <>
      {/*  */}
      {message && (
        <div className="mb-4 p-2 rounded bg-red-100 text-red-700">
          {message}
        </div>
      )}
      
      <form onSubmit={handleSubmit}>
        {/*  */}
        <div className="mb-4">
          <div className="flex items-center space-x-4 mb-4">
            {/*  */}
            <label className="flex items-center cursor-pointer">
              <input
                type="radio"
                checked={!isAdmin}
                onChange={() => setIsAdmin(false)}
                className="form-radio h-5 w-5 text-blue-600"
              />
              <span className="ml-2 text-gray-700"></span>
            </label>
            
            {/*  */}
            <label className="flex items-center cursor-pointer">
              <input
                type="radio"
                checked={isAdmin}
                onChange={() => setIsAdmin(true)}
                className="form-radio h-5 w-5 text-blue-600"
              />
              <span className="ml-2 text-gray-700"></span>
            </label>
          </div>
        </div>
        
        {/*  */}
        <div className="mb-4">
          <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="username">
            
          </label>
          <input
            id="username"
            type="text"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            required
          />
        </div>
        
        {/*  */}
        <div className="mb-4">
          <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="password">
            
          </label>
          <input
            id="password"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            required
          />
        </div>
        
        {/*  */}
        <div className="mb-6">
          <button
            type="submit"
            disabled={isLoading}
            className="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-opacity-50 transition-colors duration-300 disabled:opacity-50"
          >
            {isLoading ? '...' : ''}
          </button>
        </div>
      </form>
      
      {/*  */}
      <div className="text-center text-sm">
        <button
          onClick={switchToRegister}
          className="text-blue-600 hover:text-blue-800 focus:outline-none"
        >
          
        </button>
      </div>
    </>
  );
};

export default LoginForm;