import React from 'react';
import { Navigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';

/**
 * ProtectedRoute  - 
 * 
 * 
 * 1. 
 * 2.  vs 
 * 3. 
 * 4. 
 * 
 * Props:
 * - children: 
 * - requireAdmin:  false
 */
const ProtectedRoute = ({ children, requireAdmin = false }) => {
  // 
  const { isAuthenticated, userInfo, loading } = useAuth();

  // 
  console.log('ProtectedRoute - :', requireAdmin);
  console.log('ProtectedRoute - :', userInfo);
  console.log('ProtectedRoute - :', isAuthenticated);
  if (userInfo) {
    console.log('ProtectedRoute - :', Boolean(userInfo.isAdmin));
  }

  // 
  if (loading) {
    return (
      <div className="flex justify-center items-center h-screen">
        <p className="text-gray-500">...</p>
      </div>
    );
  }

  // 
  if (!isAuthenticated) {
    console.log('ProtectedRoute - ');
    return <Navigate to="/auth" />;
  }
  
  // 
  if (requireAdmin && !userInfo.isAdmin) {
    console.log('ProtectedRoute - ');
    return <Navigate to="/user-dashboard" />;
  }

  // 
  console.log('ProtectedRoute - ');
  return children;
};

export default ProtectedRoute;