import React, { createContext, useContext, useState, useEffect } from 'react';

// APIURL
const API_BASE_URL = 'http://localhost:3001';

// 
const AuthContext = createContext();

//  Hook
export const useAuth = () => useContext(AuthContext);

/**
 * AuthProvider  - 
 */
export const AuthProvider = ({ children }) => {
  // 
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  // 
  const [userInfo, setUserInfo] = useState(null);
  // 
  const [loading, setLoading] = useState(true);

  /**
   * API - HTTP
   */
  const apiRequest = async (endpoint, method = 'GET', data = null) => {
    console.log(`: ${API_BASE_URL}${endpoint}`);
    
    const config = {
      method,
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'include'
    };

    // 
    const token = localStorage.getItem('token');
    if (token) {
      config.headers['x-auth-token'] = token;
    }

    // 
    if (data) {
      config.body = JSON.stringify(data);
      console.log(':', data);
    }

    try {
      const response = await fetch(`${API_BASE_URL}${endpoint}`, config);
      console.log(`:  ${response.status}`);
      
      // 
      const text = await response.text();
      console.log(`: ${text.substring(0, 150)}...`);
      
      let result;
      try {
        result = text ? JSON.parse(text) : {};
        console.log(':', result);
      } catch (e) {
        console.error(':', e);
        console.error(':', text);
        throw new Error(`: ${text.substring(0, 100)}...`);
      }

      if (!response.ok) {
        throw new Error(result.message || '');
      }

      return result;
    } catch (error) {
      console.error('API:', error);
      throw error;
    }
  };

  /**
   * 
   */
  useEffect(() => {
    const checkAuth = async () => {
      //  token 
      const token = localStorage.getItem('token');
      const userStr = localStorage.getItem('user');
      
      if (token && userStr) {
        try {
          //  token 
          const result = await apiRequest('/api/user');
          
          // 
          const storedUser = JSON.parse(userStr);
          console.log(':', storedUser);
          
          // isAdmin
          const normalizedUser = {
            ...storedUser,
            isAdmin: Boolean(storedUser.isAdmin)
          };
          
          // token 
          setIsAuthenticated(true);
          setUserInfo(normalizedUser);
          
          // isAdmin
          localStorage.setItem('user', JSON.stringify(normalizedUser));
          
        } catch (error) {
          console.error(':', error);
          // 
          localStorage.removeItem('token');
          localStorage.removeItem('user');
        }
      }
      
      //  loading  false
      setLoading(false);
    };
    
    checkAuth();
  }, []);

  /**
   * 
   */
  const login = async (username, password, isAdmin) => {
    try {
      const data = await apiRequest('/api/login', 'POST', { 
        username, 
        password, 
        isAdmin 
      });
      
      console.log(':', data);
      
      // isAdmin
      const userData = {
        ...data.user,
        isAdmin: Boolean(data.user.isAdmin)
      };
      
      console.log(':', userData);
      
      //  token 
      if (data.token) {
        localStorage.setItem('token', data.token);
      }
      
      // 
      localStorage.setItem('user', JSON.stringify(userData));
      
      // 
      setIsAuthenticated(true);
      setUserInfo(userData);
      
      return data;
    } catch (error) {
      throw error;
    }
  };

  /**
   * 
   */
  const register = async (username, email, password, isAdmin, adminCode) => {
    try {
      // 
      const userData = { username, email, password, isAdmin };
      
      // 
      if (isAdmin && adminCode) {
        userData.adminCode = adminCode;
      }
      
      // 
      const data = await apiRequest('/api/register', 'POST', userData);
      return data;
    } catch (error) {
      throw error;
    }
  };

  /**
   * 
   */
  const logout = async () => {
    try {
      const token = localStorage.getItem('token');
      
      //  token
      if (token) {
        await apiRequest('/api/logout', 'POST');
      }
    } catch (error) {
      console.error(':', error);
    } finally {
      // 
      localStorage.removeItem('token');
      localStorage.removeItem('user');
      setIsAuthenticated(false);
      setUserInfo(null);
    }
  };

  /**
   * 
   */
  const authFetch = async (url, options = {}) => {
    try {
      // 
      const config = {
        method: options.method || 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
        credentials: 'include'
      };

      // 
      const token = localStorage.getItem('token');
      if (token) {
        config.headers['x-auth-token'] = token;
      }

      // 
      if (options.body) {
        config.body = options.body;
      }

      // 
      const fullUrl = url.startsWith('/') ? `${API_BASE_URL}${url}` : url;
      console.log(`authFetch: ${fullUrl}`);
      
      const response = await fetch(fullUrl, config);
      console.log(`authFetch: ${response.status}`);
      
      // 401
      if (response.status === 401) {
        logout();
        throw new Error('');
      }
      
      // responsestatus
      return {
        ok: response.ok,
        status: response.status,
        json: async () => {
          const text = await response.text();
          try {
            return text ? JSON.parse(text) : {};
          } catch (e) {
            console.error(':', e);
            return { message: text };
          }
        }
      };
    } catch (error) {
      console.error('authFetch:', error);
      return {
        ok: false,
        status: 500,
        json: async () => ({ message: error.message })
      };
    }
  };

  // 
  const value = {
    isAuthenticated,
    userInfo,
    loading,
    login,
    register,
    logout,
    authFetch
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
};